<template>
    <div id="index" class="echart">
        <div id="mapEchart" key="map"></div>
    </div>
</template>
<script>
import 'echarts/map/js/world.js';
import { wordMapName } from '../../../assets/js/echart';

export default {
    name: 'hello',
    data() {
        return {
            echartData: [],
            showTip: false,
            countType: "",
            mapData: [],
            tipOffsetX: 0,
            tipOffsetY: 0,
            codeKey: "",
            loading: false,
            langType: "cn",
        }
    },
    computed: {
        filterEchartData() {
            return this.echartData.filter((data, index) => {
                return data.count > 0;
            })
        }
    },
    watch: {
        "$route.fullPath": {
            handler() {
                this.toggleLang();
                this.setMapData();
            }
        }
    },
    mounted() {
        this.toggleLang();
        this.setMapData();
    },
    methods: {
        getMore: function () {
            var name = this.countType, codeKey = this.codeKey;
            var vuexparams = {
                codekey: codeKey,
                name: name
            };
            var params = {
                countrys: this.codeKey
            };
            this.$router.push({ name: this.langType != 'cn' ? this.langType + 'search' : 'search' });
            this.$store.commit('clearkeySerach'); //清空关键字 
            this.$store.commit('setflg', false); //标记
            this.$store.commit("setcountrsparams", vuexparams); //参数给vuex     
            this.$store.commit("search_More", params);
        },
        drawLine() {
            // 基于准备好的dom，初始化echarts实例
            const chartBox = document.getElementById('mapEchart');
            const size = chartBox.clientWidth;
            const width = size + "px";
            const height = size * 0.75 + "px";
            const mapNameData = wordMapName[this.langType];
            let myChart = this.$echarts.init(chartBox, null, {
                width,
                height,
            });
            // 绘制图表
            myChart.setOption({
                tooltip: {
                    show:false
                },
                series: [
                    {
                        name: this.langOption[this.langType].region,
                        type: 'map',
                        map: 'world',
                        selectedMode: 'single',
                        label: { show: false },
                        data: this.mapData,
                        nameMap: mapNameData,
                        opacity: 1,
                        emphasis: {
                            itemStyle: {
                                areaColor: "#fff",
                                borderColor: "#ff9800",
                                borderWidth: 1
                            },
                            label: { show: false }
                        },
                        scaleLimit: {
                            min: 1.2,
                            max: 30
                        },
                        roam: true
                    }
                ]
            }, true);
            const _this = this;
            myChart.on("click", (params) => {
                //选择了不同的地区 或者 选择了相同的地区提示框没出来
                if (params.data) {
                    _this.countType = params.data.name;
                    _this.codeKey = params.data.codeId
                    _this.getMore();
                }
            })
        },
        getProfessionalData() {
            const _this = this;
            let html = "";
            this.echartData = [];
            this.loading = true;
            this.commonMethods.emitAjax({
                path: "/api/front/psons/count/title",
                data: { countType: "country", param: this.codeKey },
                success(response) {
                    _this.loading = false;
                    _this.echartData = response;
                },
                error() {
                    _this.loading = false;
                    _this.showTip = false;
                }
            })
        },
        setMapData() {
            const _this = this;
            _this.$store.commit('fetchloading', true) //加载动画
            this.commonMethods.emitAjax({
                path: "/api/front/psons/count/country",
                success(response) {
                    _this.$store.commit('fetchloading', false)
                    _this.mapData = [];
                    _this.arr = [];
                    for (let index = 0; index < response.length; index++) {
                        const code = response[index];
                        _this.mapData.push({
                            name: code["country_simple_" + _this.langType + "_name"],
                            value: code.count,
                            codeId: code.country_code,
                        })
                    }
                    _this.drawLine();
                }
            })
        },
        toggleLang() {
            const _this = this;
            this.commonMethods.selectLangType(function (langType) {
                _this.langType = langType;
            })
        }
    }
}
</script>




